// Simple, working signup endpoint for debugging
import { AuthUtils } from "../_auth.js";

export async function onRequestPost(context) {
  const { request, env } = context;

  try {
    // Parse request body
    const { email, password, name } = await request.json();

    console.log("=== SIMPLE SIGNUP DEBUG ===");
    console.log("Request data:", {
      email,
      name,
      passwordLength: password?.length,
    });

    // Basic validation
    if (!email || !password || !name) {
      console.error("Missing required fields");
      return new Response(
        JSON.stringify({
          error: "Email, password, and name are required",
        }),
        {
          status: 400,
          headers: { "Content-Type": "application/json" },
        }
      );
    }

    // Initialize auth utilities
    const auth = new AuthUtils(env);

    // Check if user already exists
    console.log("Checking for existing user...");
    const existingUsers = await env.DB.prepare(
      "SELECT id FROM users WHERE email = ?"
    )
      .bind(email)
      .all();

    if (existingUsers?.results?.length > 0) {
      console.log("User already exists");
      return new Response(
        JSON.stringify({
          error: "User already exists",
        }),
        {
          status: 409,
          headers: { "Content-Type": "application/json" },
        }
      );
    }

    // Generate user ID
    const userId = `simple_user_${Date.now()}`;
    console.log("Generated user ID:", userId);

    // Hash password
    console.log("Hashing password...");
    const passwordHash = await auth.hashPassword(password);
    console.log("Password hash length:", passwordHash?.length);

    // Insert user
    console.log("Inserting user into database...");
    const insertResult = await env.DB.prepare(
      "INSERT INTO users (id, email, name, password_hash) VALUES (?, ?, ?, ?)"
    )
      .bind(userId, email, name, passwordHash)
      .run();

    console.log("Insert result:", insertResult);

    // Check if insertion was successful
    if (!insertResult.success) {
      console.error("Insertion failed:", insertResult);
      return new Response(
        JSON.stringify({
          error: "Failed to create user: " + JSON.stringify(insertResult),
        }),
        {
          status: 500,
          headers: { "Content-Type": "application/json" },
        }
      );
    }

    console.log("User successfully inserted");

    // Verify by querying back
    const verifyResult = await env.DB.prepare(
      "SELECT id, email, name FROM users WHERE email = ?"
    )
      .bind(email)
      .all();

    console.log("Verification result:", verifyResult);

    if (!verifyResult?.results?.length) {
      console.error("User not found after insertion!");
      return new Response(
        JSON.stringify({
          error: "User creation failed verification",
        }),
        {
          status: 500,
          headers: { "Content-Type": "application/json" },
        }
      );
    }

    console.log("User verified successfully");

    // Generate JWT token
    const token = auth.generateToken(userId, email);
    console.log("Token generated:", token?.substring(0, 20) + "...");

    // Create response data
    const userData = {
      id: userId,
      email,
      name,
      created_at: new Date().toISOString(),
    };

    console.log("=== SIMPLE SIGNUP SUCCESS ===");
    console.log("Returning success response");

    // Return success response with correct format
    return new Response(
      JSON.stringify({
        user: userData,
        token: token,
        message: "User created successfully",
      }),
      {
        status: 201,
        headers: { "Content-Type": "application/json" },
      }
    );
  } catch (error) {
    console.error("=== SIMPLE SIGNUP ERROR ===");
    console.error("Error details:", error);
    console.error("Stack:", error.stack);

    return new Response(
      JSON.stringify({
        error: "Internal server error: " + error.message,
      }),
      {
        status: 500,
        headers: { "Content-Type": "application/json" },
      }
    );
  }
}
